
.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 100%;

}

.App-logo {
  height: 100px;
}

.motion {
  /*border:1px solid black;*/
  width: 100px;
  height: 100px;
}

.App-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;

}

.App-intro {
  text-align:center;
  color: black;
  height: 80px;
}


@media all and (max-width: 400px) {
  .motion {
    width: 78px;
    height: 70px;
  }
  .App-logo {
    height: 75px;
  }
  .App-header {
    height: 500px;
  }
}
@media all and (min-width: 401px) {
  .App-header {
    height: 600px;
  }
}

@media all and (min-width: 1400px) {
  .App-header {
    height: 900px;
  }
}
